// import { onMounted, ref } from 'vue';
import { ref } from 'vue';
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'
import axios from 'axios'
// import { pathToAPI, global } from '@/data/globals.js'
import { define } from '@/data/globals.js'

export default function useAuth () {
  const email = ref('vasya2@coder.com')
  const password = ref('888')
  // const email = ref('')
  // const password = ref('')
  // const host = useStore(host)
  // const domain = useRouter(domain)
  
  const store = useStore()
  const router = useRouter()
  // const likes = ref(store.state.storeLikes + 1)

  async function login() {
    try {
      const response = await axios.post( define.pathToAPI + "/login.php", JSON.stringify({
        email: email.value,
        password: password.value,
        // jwtin: 'Bearer YOUR_JWT_TOKEN_HERE',
        }),
        {
          withCredentials: true,
          headers: {
            // Authorization: 'Bearer YOUR_JWT_TOKEN_HERE',
            Accept: 'application/json',
            'Content-Type': 'application/json',
          }
        }
      ) // axios

      // const response = await axios({
      //   method: 'GET',
      //   url: 'https://jsonplaceholder.typicode.com/posts',
      //   headers: {
      //       // 'authorization': 'Bearer YOUR_JWT_TOKEN_HERE'
      //       'Content-type': '111 ',
      //       'X-Requested-With': 'XMLHttpRequest'
      //   }
      // })
      
      console.log('response: ', response);
      console.log('response.status: ', response.status);
      
      store.commit('login')
      router.push('/')

      // axios.defaults.headers.common['Authorization'] = "Bearer"

    } catch (e) {
      // console.log('e: ', e);
      switch(e.response.status) {
        case 0:
          console.log('Нет соединения с сервером')
          console.log('Сделать пуш уведомление');
          break
        case 401:
          console.log('Логин или пароль указаны неверно / ' + e.response.data.message)
          console.log('Сделать пуш уведомление');
          break
        default:
          console.log(e)
          console.log('Сделать пуш уведомление');
          break
      }
    }// try
  
  } // login

  function logout() {
    console.log('logOut')
    // // await this.$store.dispatch('logout')
    // await this.$store.dispatch('logout')
    store.commit('logout')
    // this.$router.push('/login')
    localStorage.removeItem('AccessToken');
  } // logout

  // onMounted(addLike)
  return { email, password, login, logout, define }

}