import { createStore } from 'vuex'

export default createStore({

  state() {
    return {
      isAuth: false,
      storeLikes: 4,
    }
  },

  getters: {
  },

  mutations: {
    login(state) {
      state.isAuth = true
      // console.log('mutations')
    },
    logout(state) {
      state.isAuth = false
      // console.log('mutations')
    },
    increment(state) {
      state.storeLikes += 1
    },
  },

  actions: {
  },

  modules: {
  }

})
