import { createRouter, createWebHistory } from 'vue-router'
import CatalogView from '@/views/CatalogView.vue'
// import PageNotFound from '@/views/PageNotFound'

const routes = [
  {
    path: '/', // name: 'home',
    component: CatalogView
  },
  {
    path: '/:id', // name: 'about',
    component: () => import('../views/CarByIdView.vue') // lazy-loaded when the route is visited.
  },
  {
    path: '/add', // name: 'about',
    component: () => import('../views/AddView.vue') // lazy-loaded when the route is visited.
  },
  {
    path: '/login', // name: 'about',
    component: () => import('../views/LoginView.vue') // lazy-loaded when the route is visited.
  },
  {
    path: '/about', // name: 'about',
    component: () => import('../views/AboutView.vue') // lazy-loaded when the route is visited.
  },
  // {
  //   path: '/:catchAll(.*)*',
  //   name: "PageNotFound",
  //   component: PageNotFound,
  // },
  // {
  //   path: '/cars/:id', // name: 'about',
  //   component: () => import('../views/CarByIdView.vue') // lazy-loaded when the route is visited.
  // },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  base: "/vue-auto/",
  routes
})

export default router
